import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-product-variant',
    styleUrl: 'product-variant.css'
})
export class ProductVariant {

    @Prop() image: string;
    @Prop() webp: string;
    @Prop() name: string;
    @Prop() link: string;
    @Prop({reflect: true}) active: boolean;
    @Prop({reflect: true}) unavailable: boolean;

	render() {
		return(
            <a href={this.link} aria-label={this.name}>
                <ks-img2 src={this.image} webp={this.webp} width={90} height={90} alt={this.name}/>
            </a>
		);
	}
}
